@use "_variables" as *;

@mixin small-type {
    font-family: $primary-font;
    font-size: $base-font-size;
    font-variation-settings: "wght" $base-font-weight;
}

@mixin small-type-link {
    font-family: $primary-font;
    font-size: $base-font-size;
    font-variation-settings: "wght" calc($base-font-weight + 100);
    text-decoration: none;
}

@mixin large-type {
    font-family: $primary-font;
    font-size: calc($base-font-size * $scale-factor);
    font-variation-settings: "wght" calc($base-font-weight / $scale-factor);
}

h1 {
    @include large-type;
}

p {
    @include large-type;
}

a {
    font-family: $primary-font;
    font-size: $base-font-size;
    font-variation-settings: "wght" calc($base-font-weight + 100);
    text-decoration: none;
}

#logo {
    text-transform: uppercase;
}